import React from "react"
import _get from "lodash/get"
import { useStaticQuery, graphql } from "gatsby"

import Img from "gatsby-image"

const InstaFeed = () => {
  const data = useStaticQuery(graphql`
    query InstagramQuery {
      allInstagramContent {
        edges {
          node {
            localImage {
              childImageSharp {
                fixed(width: 300, height: 300) {
                  ...GatsbyImageSharpFixed
                }
              }
            }
          }
        }
      }
    }
  `)

  let arrayOfInstaImages = _get(data, "allInstagramContent.edges")
  return (
    <div
      style={{ 
          
          marginTop: `3rem`,
          marginBottom: `3rem`, 
          display: "grid", 
          gridTemplateColumns:"300px 300px 300px 300px 300px",
          gridGap: '10px'
         }}
    >
      {arrayOfInstaImages.map((item, i) => {
        return (
          <div key={i} style={{ backgroundClip: "black" }}>
            <Img fixed={item.node.localImage.childImageSharp.fixed} />
          </div>
        )
      })}
    </div>
  )
}

export default InstaFeed
